import axios from "axios";

const API_URL = "https://shop.evpakademi.com/api"; // API'nin temel URL'si

//#region product
export const GetAllProducts = async () => {
  try {
    const response = await axios.get(`${API_URL}/product/get`);
    return response.data;
  } catch (error) {
    console.error("rnler alnamad:", error);
    return null; // Hata durumunda null dndryoruz
  }
};
export const addProduct = async (productData) => {
  try {
    const response = await axios.post(`${API_URL}/product/add`, productData, {
      headers: { "Content-Type": "multipart/form-data" },
    });
    return response.data;
  } catch (error) {
    throw (
      error.response?.data || "rn eklenirken/gncellenirken bir hata olutu."
    );
  }
};
export const updateProduct = async (id, productData) => {
  try {
    const response = await axios.put(
      `${API_URL}/product/update/${id}`,
      productData,
      {
        headers: { "Content-Type": "multipart/form-data" },
      }
    );
    return response.data;
  } catch (error) {
    throw error.response?.data || "rn gncellenirken bir hata olutu.";
  }
};
export const deleteProduct = async (id) => {
  try {
    const response = await axios.delete(`${API_URL}/product/delete/${id}`);
    return response.data;
  } catch (error) {
    throw error.response?.data || "rn silinirken bir hata olutu.";
  }
};
//#endregion
