//#region trademark
export const getTrademarks = async () => {
  try {
    const response = await axios.get(`${API_URL}/trademark/get`);
    return response.data;
  } catch (error) {
    throw error.response?.data || "Trademarklar alrken bir hata olutu.";
  }
};
export const addTrademarkHG = async (trademark, image_data) => {
  try {
    const formData = new FormData();
    formData.append("name", trademark);
    formData.append("image_data", image_data);

    for (let pair of formData.entries()) {
      console.log(pair[0], pair[1]); // Name ve image_data doru mu?
    }

    const response = await axios.post(`${API_URL}/trademark/add`, formData, {
      headers: { "Content-Type": "multipart/form-data" },
    });
    return response.data;
  } catch (error) {
    throw error.response?.data || "Trademark eklenirken bir hata olutu.";
  }
};
export const updateTrademarkHG = async (id, name, imageFile = null) => {
  try {
    let formData = new FormData();
    formData.append("name", name);

    if (imageFile) {
      formData.append("image", imageFile);
    }

    const response = await axios.put(
      `${API_URL}/trademark/update/${id}`,
      formData,
      {
        headers: {
          "Content-Type": "multipart/form-data",
        },
      }
    );

    return response.data;
  } catch (error) {
    throw error.response?.data || "Marka gncellenirken bir hata olutu.";
  }
};
export const deleteTrademark = async (id) => {
  try {
    const response = await axios.delete(`${API_URL}/trademark/delete/${id}`);
    return response.data;
  } catch (error) {
    throw error.response?.data || "Trademark silinirken bir hata olutu.";
  }
};
