import React, { createContext, useState, useContext, useEffect } from "react";

// CartContext oluturuyoruz
const CartContext = createContext();

// Hook kullanm
export const useCart = () => {
  return useContext(CartContext);
};

// CartProvider Component'i
export const CartProvider = ({ children }) => {
  const [cartItems, setCartItems] = useState([]);

  // LocalStorage'dan sepeti ykle
  useEffect(() => {
    const savedCart = localStorage.getItem("cartItems");
    if (savedCart) {
      setCartItems(JSON.parse(savedCart));
    }
  }, []);

  // LocalStorage'a sepeti kaydet
  useEffect(() => {
    localStorage.setItem("cartItems", JSON.stringify(cartItems));
  }, [cartItems]);

  // Sepete rn ekleme
  const addToCart = (product) => {
    if (!product || !product.id) {
      console.error("Geersiz rn verisi:", product);
      return;
    }

    const existingItem = cartItems.find((item) => item.id === product.id);

    if (existingItem) {
      if (existingItem.quantity < product.stockQuantity) {
        setCartItems((prevItems) =>
          prevItems.map((item) =>
            item.id === product.id
              ? { ...item, quantity: item.quantity + 1 }
              : item
          )
        );
      } else {
        console.error("Stok miktar ald.");
      }
    } else {
      if (product.stockQuantity > 0) {
        const newProduct = {
          id: product.id,
          name: product.name,
          price: product.price,
          image_data: product.image_data || null,
          discountRate: product.discountRate || 0,
          stockQuantity: product.stockQuantity,
          description: product.description || "Aklama yok.",
          quantity: 1,
        };
        setCartItems((prevItems) => [...prevItems, newProduct]);
      } else {
        console.error("Stokta rn yok.");
      }
    }
  };

  // Sepet esi miktarn gncelle
  const updateCartItemQuantity = (productId, quantity) => {
    setCartItems((prevItems) =>
      prevItems.map((item) =>
        item.id === productId
          ? {
              ...item,
              quantity: Math.min(quantity, item.stockQuantity), // Stok snrna dikkat
            }
          : item
      )
    );
  };

  // Sepetten rn kar
  const removeFromCart = (productId) => {
    setCartItems((prevItems) =>
      prevItems.filter((item) => item.id !== productId)
    );
  };

  // Sepeti temizle
  const clearCart = () => {
    setCartItems([]);
  };

  // Sepet iin ek bir kontrol veya ilem yapmak isteyenler iin bu fonksiyonu dndryoruz
  const cartTotal = () => {
    return cartItems
      .reduce((total, item) => total + item.price * item.quantity, 0)
      .toFixed(2);
  };

  return (
    <CartContext.Provider
      value={{
        cartItems, // Sepet eleri
        setCartItems, // Sepet zerinde dorudan ilem yaplabilmesi iin
        addToCart, // Sepete rn ekle
        removeFromCart, // Sepetten rn kar
        clearCart, // Sepeti temizle
        updateCartItemQuantity, // Sepet esi miktarn gncelle
        cartTotal, // Sepetin toplam maliyetini dndren hesaplama fonksiyonu
      }}
    >
      {children}
    </CartContext.Provider>
  );
};
