import React, { useState } from "react";
import Cards from "react-credit-cards";
import axios from "axios";
import { useLocation } from "react-router-dom";
import { useCart } from "../context/CartContext"; // Sepet verilerini almak iin import ettik.
import "react-credit-cards/es/styles-compiled.css";
import "./Payment.css";

function Payment() {
  const location = useLocation();
  const { discountedPrice } = location.state || { discountedPrice: 0 }; // ndirimli tutar al
  const { cartItems } = useCart(); // Sepet verilerini ekiyoruz.

  // Sepet toplamn hesapla
  const totalAmount = cartItems.reduce(
    (total, item) => total + item.price * item.quantity,
    0
  );

  const [cardInfo, setCardInfo] = useState({
    number: "",
    name: "",
    expiration: "",
    cvc: "",
  });

  const handleChange = (e) => {
    setCardInfo({ ...cardInfo, [e.target.name]: e.target.value });
  };

  const handlePayment = async () => {
    try {
      const response = await axios.post(
        "http://localhost:5000/initiate-payment",
        {
          amount: discountedPrice || totalAmount, // ndirimli tutar veya sepet toplamn gnder
          ...cardInfo,
        }
      );

      if (response.data.success) {
        alert("deme baarl!");
      } else {
        alert("deme baarsz.");
      }
    } catch (error) {
      alert("Bir hata olutu.");
    }
  };

  return (
    <div className="payment-container">
      <div className="credit-card-preview">
        <Cards
          number={cardInfo.number}
          name={cardInfo.name}
          expiry={cardInfo.expiration}
          cvc={cardInfo.cvc}
          focused={"number"}
        />
      </div>

      <div className="payment-section">
        <input
          type="text"
          placeholder="Kart Numaras"
          name="number"
          value={cardInfo.number}
          onChange={handleChange}
        />
        <input
          type="text"
          placeholder="sim"
          name="name"
          value={cardInfo.name}
          onChange={handleChange}
        />
        <input
          type="text"
          placeholder="Son Kullanma Tarihi (MM/YY)"
          name="expiration"
          value={cardInfo.expiration}
          onChange={handleChange}
        />
        <input
          type="text"
          placeholder="CVV"
          name="cvc"
          value={cardInfo.cvc}
          onChange={handleChange}
        />
        <button onClick={handlePayment} className="pay-btn">
          deme Yap
        </button>
      </div>

      {/* deme tutarn ekrana yazdr */}
      <p>
        deme Tutar:{" "}
        {discountedPrice > 0
          ? discountedPrice.toFixed(2)
          : totalAmount.toFixed(2)}{" "}
        TL
      </p>
    </div>
  );
}

export default Payment;
