import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import AuthService from "../../Apis/auth.service";
import "./Order.css";
import {
  Box,
  Typography,
  Card,
  CardMedia,
  CardContent,
  Grid,
  CircularProgress,
} from "@mui/material";
import CheckCircleIcon from "@mui/icons-material/CheckCircle";
import LocalShippingIcon from "@mui/icons-material/LocalShipping";
import HourglassTopIcon from "@mui/icons-material/HourglassTop";

// Durumlar iin simge dndren fonksiyon
const getStatusIcon = (status) => {
  if (status === "Sipari Alnd") return <HourglassTopIcon color="warning" />;
  if (status === "Kargoya Verildi")
    return <LocalShippingIcon color="primary" />;
  if (status === "Teslim Edildi") return <CheckCircleIcon color="success" />;
  return <HourglassTopIcon />;
};

const Order = () => {
  const navigate = useNavigate();
  const [orders, setOrders] = useState([]); // Siparileri ekmek iin state
  const [loading, setLoading] = useState(true); // Ykleme ilemi iin state
  const [error, setError] = useState(""); // Hata ynetimi iin state

  // Sipari ekme ilemi
  useEffect(() => {
    if (!AuthService.getCurrentUser()) {
      navigate("/login"); // Kullanc giri yapmamsa giri sayfasna ynlendir
    } else {
      const fetchOrders = async () => {
        try {
          const response = await fetch(
            `http://localhost:5000/orders/${
              AuthService.getCurrentUser().user.id
            }`
          );
          if (!response.ok) {
            throw new Error("Sipariler ekilirken bir hata olutu.");
          }

          const data = await response.json();

          console.log(AuthService.getCurrentUser());
          console.log("Veritabanndan ekilen sipariler: ", data);

          const formattedOrders = data.map((order) => ({
            ...order,
            items:
              typeof order.items === "string"
                ? JSON.parse(order.items)
                : order.items,
          }));

          setOrders(formattedOrders);
          setLoading(false);
        } catch (error) {
          setError(error.message);
          setLoading(false);
        }
      };

      fetchOrders();
    }
  }, []);

  if (loading) {
    return (
      <Box textAlign="center" mt={4}>
        <CircularProgress />
        <Typography>Ykleniyor...</Typography>
      </Box>
    );
  }

  if (error) {
    return (
      <Box textAlign="center" mt={4}>
        <Typography color="error">{error}</Typography>
      </Box>
    );
  }

  return (
    <div>
      {AuthService.getCurrentUser() && (
        <Box p={4} sx={{ fontFamily: "Roboto, sans-serif" }}>
          <Typography variant="h4" gutterBottom>
            Sipari Listesi
          </Typography>

          {/* Sipari Listesi */}
          <Grid container spacing={2}>
            {orders.length > 0 ? (
              orders.map((order) => (
                <Grid item xs={12} sm={6} md={4} key={order.id}>
                  <Card>
                    <CardContent>
                      <Typography>
                        <strong>Sipari Numaras:</strong> {order.order_number}
                      </Typography>
                      <Typography>
                        <strong>Kullanc ID:</strong> {order.user_id}
                      </Typography>
                      <Typography>
                        <strong>Sipari Tarihi:</strong>{" "}
                        {new Date(order.order_date).toLocaleString()}
                      </Typography>
                      <Typography>
                        <strong>Teslim Tarihi:</strong>{" "}
                        {new Date(order.delivery_date).toLocaleString()}
                      </Typography>
                      <Typography>
                        <strong>Durum:</strong> {order.status}
                      </Typography>
                      <Typography>
                        <strong>rnler:</strong>
                      </Typography>
                      {order.items.length > 0 ? (
                        order.items.map((item, index) => (
                          <Box key={index} mt={1} sx={{ paddingLeft: 1 }}>
                            <Typography>
                              - {item.name} | Adet: {item.quantity} | Fiyat:{" "}
                              {Number(item.price).toFixed(2)}
                            </Typography>
                          </Box>
                        ))
                      ) : (
                        <Typography>rn bulunmuyor.</Typography>
                      )}
                    </CardContent>
                  </Card>
                </Grid>
              ))
            ) : (
              <Grid item xs={12}>
                <Typography textAlign="center" mt={2}>
                  Sipari bulunmamaktadr.
                </Typography>
              </Grid>
            )}
          </Grid>
        </Box>
      )}
    </div>
  );
};

export default Order;
